.switch {
  width: 40px;
  height: 16px;
  margin: 0 10px;
  display: block;
  transform: translateY(-25%);
}

.slider {
  cursor: pointer;
  border: 4px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: 0 0 0 2px #ff001b, 0 0 4px #ff001b;
}

.slider:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ff001b;
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  transform: translateX(-24px);
}

@media screen and (min-width: 768px) {
  .slider:before {
    transform: translateX(-20px);
  }
}

input:checked + .slider:before {
  background-color: #ff001b;
  transform: translateX(24px);
}

@media screen and (min-width: 768px) {
  input:checked + .slider:before {
    transform: translateX(20px);
  }
}

input:checked + .slider {
  box-shadow: 0 0 0 2px #ff001b, 0 0 8px #ff001b;
}

.switch200 .slider:before {
  width: 200%;
  transform: translateX(-82px);
}

